import React, { PureComponent } from "react";

import { Helmet } from "react-helmet";

import axios from "axios";
import moment from "moment-timezone";
import { Link } from "react-router-dom";
import {
    Avatar, Divider,
    List, Icon, Skeleton,
} from "antd";
import API_KEY from '../../utils/apiKeys';

const USERNAME = 'marllonfrizzo';
const ROOT_URL = 'https://ws.audioscrobbler.com/2.0/'

export default class musica extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            info: null,
            tracks: [],
            topArtists: [],
            loadingInfo: false,
            loadingArtists: false,
            loadingRecentTracks: false,
        };
    }

    componentDidMount() {
        this._requestInfo();
        this._requestRecentTracks();
        this._requestTopArtists();
    }

    _requestTopArtists = () => {
        this.setState({ loadingArtists: true });
        return axios.get(`${ROOT_URL}?method=user.gettopartists&user=${USERNAME}&api_key=${API_KEY}&format=json&limit=5&period=7day`)
            .then(response => {
                const { data } = response;
                this.setState({
                    topArtists: data.topartists.artist,
                })
            })
            .catch(ex => {
                console.warn(ex);
            })
            .finally(() => {
                this.setState({ loadingArtists: false });
            })
    }

    _requestRecentTracks = () => {
        this.setState({ loadingRecentTracks: true });
        return axios.get(`${ROOT_URL}?limit=10&method=user.getrecenttracks&user=${USERNAME}&api_key=${API_KEY}&format=json&extended=1`)
            .then(response => {
                const { data } = response;
                this.setState({
                    tracks: data.recenttracks.track,
                })
            })
            .catch(ex => {
                console.warn(ex);
            })
            .finally(() => {
                this.setState({ loadingRecentTracks: false });
            })
    }

    _requestInfo = () => {
        this.setState({ loadingInfo: true });
        return axios.get(`${ROOT_URL}?method=user.getinfo&user=${USERNAME}&api_key=${API_KEY}&format=json`)
            .then(response => {
                const { data } = response;
                this.setState({
                    info: data.user,
                })
            })
            .catch(ex => {
                console.warn(ex);
            })
            .finally(() => {
                this.setState({ loadingInfo: false });
            });
    }

    _renderInfo = () => {
        const { info } = this.state;

        if (!info) return null;

        const {
            name, realname, country, url,
            registered, playcount, image,
        } = info;

        const date = moment.unix(registered.unixtime);
        return (
            <div>
                <h3>
                    <span>{realname} ({name}) </span>
                    <a href={url} target="_blank" rel="noopener noreferrer">
                        <Icon type="link" theme="outlined" />
                    </a>
                </h3>

                <div style={{ display: 'flex', flexWrap: 'wrap' }}>
                    <div style={{ width: '70px' }}>
                        <Avatar shape="square" size={64} src={image[1]['#text']} />
                    </div>
                    <div style={{ flex: '1' }}>
                        Usurio desde <strong>{date.locale('pt-br').format('LL')}</strong><br />
                        Total de reprodues: <strong>{playcount}</strong><br />
                        Pas: <strong>{country}</strong><br />
                    </div>
                </div>
            </div>
        )
    }

    _formatItemList = (artist, album) => (
        <span>Por <strong>{artist}</strong>, do lbum <strong>{album}</strong></span>
    )

    _renderRecentTracks = () => {
        const { tracks, loadingRecentTracks } = this.state;

        if (!tracks) return null;

        return (
            <List
                itemLayout="horizontal"
                loading={loadingRecentTracks}
                pagination={false}
                dataSource={tracks}
                renderItem={item => (
                    <List.Item>
                        <List.Item.Meta
                            avatar={<Avatar src={
                                item.image[3]['#text']}
                                size={64}
                            />}
                            title={
                                <span>
                                    <span>{item.name} </span>
                                    {item.loved === '1'
                                        ? (
                                            <Icon
                                                type="heart"
                                                theme="filled"
                                                style={{
                                                    color: 'red'
                                                }}
                                            />
                                        )
                                        : null
                                    }
                                </span>
                            }
                            description={this._formatItemList(item.artist.name, item.album['#text'])}
                        />
                        {item['@attr']
                            ? 'Reproduzindo agora'
                            : moment.unix(item.date.uts).locale('pt-br').format('lll')
                        }
                    </List.Item>
                )}
            />
        );
    }

    _renderTopArtists = () => {
        const { topArtists, loadingArtists } = this.state;

        if (!topArtists.length) return null;
        return (
            <List
                itemLayout="horizontal"
                loading={loadingArtists}
                pagination={false}
                dataSource={topArtists}
                renderItem={item => (
                    <List.Item>
                        <List.Item.Meta
                            avatar={<Avatar
                                src={item.image[3]['#text']}
                                size="large"
                            />}
                            title={item.name}
                        />
                        <span><strong>{item.playcount}</strong> reprodues</span>
                    </List.Item>
                )}
            />
        );
    }

    render() {
        const { loadingInfo } = this.state;
        return (
            <div>
                <Helmet>
                    <title>Minhas msicas</title>
                    <script async src="https://www.googletagmanager.com/gtag/js?id=UA-61529437-3"></script>
                    <script>{`
                    window.dataLayer = window.dataLayer || [];
                    function gtag(){dataLayer.push(arguments);}
                    gtag('js', new Date());
                  
                    gtag('config', 'UA-61529437-3');

                `}</script>
                    <meta charSet="utf8" />
                </Helmet>
                <h1>Minhas msicas</h1>
                <Skeleton active avatar loading={loadingInfo}>
                    {this._renderInfo()}
                </Skeleton>
                <Divider>ltimas 10 msicas reproduzidas</Divider>
                {this._renderRecentTracks()}
                <Divider>Top 5 artistas da ltima semana</Divider>
                {this._renderTopArtists()}
                <div style={{ marginTop: '35px' }}>
                    <Link to="./">Voltar</Link>
                </div>
            </div>
        );
    }

};



